import request from '@/utils/request'

/**
 * @Author zhuxiaolong@dth618.com
 * @Date 2019.11.15
 * @Param { FormData }<type:object></type:object>
 * @Commit 登录接口
 * */
export function loginAction(data) {
  return request({
    url: '/user/login',
    method: 'post',
    data
  })
}

/**
 * @Author zhuxiaolong@dth618.com
 * @Date 2019.11.15
 * @Param { string }<type:string></type:string>
 * @Commit 用户信息查询接口
 * */
export function userInfoAction(token) {
  return request({
    url: '/user/info',
    method: 'get',
    params: { token }
  })
}

/**
 * @Author zhuxiaolong@dth618.com
 * @Date 2019.11.21
 * @Param { data }<type:object></type:object>
 * @Commit 用户信息查询接口
 * */
export function getRoleByUserId(data) {
  return request({
    url: '/admin/role/listByUserId',
    method: 'post',
    data
  })
}

/**
 * @Author zhuxiaolong@dth618.com
 * @Date 2019.11.21
 * @Param { data }<type:object></type:object>
 * @Commit 根据角色ID查询对应权限接口
 * */
export function getRolesListById(data) {
  return request({
    url: '/admin/permission/list',
    method: 'post',
    data
  })
}

/**
 * @Author zhuxiaolong@dth618.com
 * @Date 2019.11.15
 * @Param { void }<type:null></type:null>
 * @Commit 退出接口
 * */
export function logoutAction() {
  return request({
    url: '/user/logout',
    method: 'post'
  })
}
