// import Cookies from 'js-cookie'

const state = {
  logo: {
    opened: 0
  }
}

const mutations = {
  TOGGLE_LOGO: state => {
    state.logo.opened = !state.logo.opened
  }
}

const actions = {
  toggleLogo({ commit }) {
    commit('TOGGLE_LOGO')
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
