// import Cookie from 'js-cookie'
import { getSession, setSession, removeSession } from '@/utils/auth'
import { loginAction, getRoleByUserId, getRolesListById } from '@/api/user'
const state = {
  session: getSession(),
  name: '',
  avatar: '',
  introduction: '',
  roles: [],
  userId: '',
  roleObj: null
}

const mutations = {
  SET_SESSION: (state, session) => {
    state.session = session
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_INTRODUCTION: (state, instruction) => {
    state.instruction = instruction
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  SET_USERID: (state, userId) => {
    state.userId = userId
  },
  SET_ROLEOBJ: (state, roleObj) => {
    state.roleObj = roleObj
  }
}

const actions = {
  doLogin({ commit }, loginForm) {
    const { userName, passWord } = loginForm
    debugger
    const session = 'M2ViNzdkMDktODQ3NC00YzBlLTlkY2YtMmVhMGUzOTZjOTBj'
    return new Promise((resolve, reject) => {
      loginAction({ userName: userName.trim(), passWord: passWord }).then(resp => {
        const { data } = resp
        commit('SET_SESSION', session)
        commit('SET_USERID', data.id)
        setSession(session)
        resolve(resp)
      }).catch(error => {
        commit('SET_SESSION', '')
        commit('SET_ROLES', [])
        removeSession(session)
        reject(error)
      })
    })
  },
  doUserInfo({ commit }, userId) {
    return new Promise((resolve, reject) => {
      getRoleByUserId({ id: userId }).then(resp => {
        const roleObj = resp.data[0]
        const roles = []
        roles.push(roleObj.role)
        commit('SET_ROLES', roles)
        commit('SET_ROLEOBJ', roleObj)
        resolve(resp)
      })
    })
  },
  doRolesListById({ commit }, roleObj) {
    const { page, size } = roleObj
    return new Promise((resolve, reject) => {
      getRolesListById({ page: page, size: size }).then(resp => {
        resolve(resp)
      })
    })
  },
  doLoginOut({ commit }) {
    return new Promise(resolve => {
      commit('SET_SESSION', '')
      commit('SET_ROLES', [])
      removeSession()
      resolve()
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
