import Vue from 'vue'
import VueRouter from 'vue-router'
import History from '../views/History.vue'
import Login from '../views/login/index.vue'
import Layout from '../layout/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/history',
    component: Layout,
    children: [{
      path: 'history',
      meta: {
        title: '历史记录'
      },
      component: History
    }]
  },
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登录'
    },
    component: Login
  },
  {
    path: '/current',
    component: Layout,
    redirect: '/current/index',
    children: [{
      path: 'index',
      meta: {
        title: '当前业务'
      },
      component: () => import('../views/Current.vue')
    }]
  },
  {
    path: '/schedule',
    component: Layout,
    redirect: '/schedule/index',
    children: [{
      path: 'index',
      meta: {
        title: '待办事项'
      },
      component: () => import('../views/Schedule.vue')
    }]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
